
/* Basier Circle */

@font-face {
    font-family: 'Basier Circle';
    src: url('/assets/fonts/basiercircle-bolditalic-webfont.eot');
    src: url('/assets/fonts/basiercircle-bolditalic-webfont.eot?#iefix') format('embedded-opentype'),
         url('/assets/fonts/basiercircle-bolditalic-webfont.woff2') format('woff2'),
         url('/assets/fonts/basiercircle-bolditalic-webfont.woff') format('woff'),
         url('/assets/fonts/basiercircle-bolditalic-webfont.ttf') format('truetype');
    font-weight: 900;
    font-style: italic;

}

